<template>
	<div class="product-buy-wrapper">
		<div class="product-buy-header">
			<div class="header-left">
				<span class="solution-top">激活码购买</span>
				<img
					src="../../assets/images/back.png"
					class="back"
					title="返回上一层"
					@click="back"
				/>
			</div>
			<div class="header-right"></div>
		</div>
		<div class="product-buy-content">
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						商品信息
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<img
						:src="
							product_logo || require('../../assets/images/product_logo.png')
						"
						class="product-logo"
					/>
					<div class="product-table">
						<el-table :data="tableData" style="width: 100%">
							<el-table-column label="商品名称" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.chName">
										{{ scope.row.chName }}
									</div>
								</template>
							</el-table-column>
							<el-table-column label="商品英文名称" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.enName">
										{{ scope.row.enName }}
									</div>
								</template>
							</el-table-column>
							<el-table-column label="购买数量" align="center" width="100">
								<template slot-scope="scope">
									<div class="cut-area">*{{ scope.row.product_num }}</div>
								</template>
							</el-table-column>
							<el-table-column label="单价" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.price">
										<span class="original-price">￥{{ (+scope.row.originalPrice).toFixed(2) }}</span>
										<span class="now-price">￥{{ (+scope.row.actualPrice).toFixed(2) }}</span>
										<!-- <span>（上新价）</span> -->
									</div>
								</template>
							</el-table-column>
						</el-table>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						信息填写
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="flex-center">
						<div class="info-wrapper">
							<div class="active-num">
								<div class="input-title">要激活的个数：</div>
								<el-input v-model="active_nums">
									<!-- <template slot="suffix"> </template> -->
									<template slot="append">
										<div class="active-btn">
											<i
												class="el-icon-caret-top activeIcon"
												@click="activeUp"
											></i>
											<i
												class="el-icon-caret-bottom activeIcon"
												@click="activeDown"
											></i>
										</div>
									</template>
								</el-input>
							</div>
							<div class="sn-input">
								<div class="input-title">要绑定的产品SN：</div>
								<div class="input-area-wrapper">
									<div
										class="input-area"
										v-for="(item, index) in sn_arr"
										:key="index"
									>
										<el-input v-model="item.value"></el-input>

										<img
											src="../../assets/images/problem.png"
											class="problem"
										/>
										<span class="problem-value"
											>点击{{
												tableData[0].chName
											}}帮助获取SN码（XXXXXXXXXXXX）</span
										>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						支付方式
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content payWay">
					<el-table
						:data="pay_arr"
						style="width: 100%"
						@row-click="changePayWay"
					>
						<el-table-column label="名称" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.name">
									<el-radio v-model="pay_way" :label="scope.row.name">
										<img
											v-if="scope.row.type === 'icon'"
											:src="scope.row.url"
											class="pay-icon"
											:class="{ card: scope.row.name == '卡号支付' }"
										/>
										<span class="pay-name">{{ scope.row.name }}</span>
									</el-radio>
								</div>
							</template>
						</el-table-column>
						<el-table-column label="订购描述" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.description">
									{{ scope.row.description }}
								</div>
							</template>
						</el-table-column>
						<el-table-column label="手续费" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.service_charge">
									{{ scope.row.service_charge }}
								</div>
							</template>
						</el-table-column>
					</el-table>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						其他信息
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="flex-start">
						<div class="request-bill">开发票：</div>
						<el-checkbox v-model="is_invoice"></el-checkbox>
						<span class="invoice-item">发票类型：</span>
						<el-select v-model="invoice_type" placeholder="请选择">
							<el-option
								v-for="item in options"
								:key="item.value"
								:label="item.label"
								:value="item.value"
							>
							</el-option>
						</el-select>
						<span class="invoice-item">发票抬头：</span>
						<el-input></el-input>
						<span class="invoice-item">发票内容：</span>
						<el-input></el-input>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						费用总计
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="total-price-content">
						<div class="divide-area">
							<div class="total-price">商品总价：￥ {{ totalPrice }}</div>
							<div class="real-price-wrapper">
								<span class="total-price">实付金额：</span>
								<span class="real-price"
									>￥ {{ pay_way == "卡号支付" ? '0.00' : totalPrice }}</span
								>
							</div>
						</div>
						<div class="divide-area space-area">
							<div>
								<el-checkbox v-model="is_agree">我已阅读</el-checkbox>
								<span class="buy-agreement" @click="TemasekDialog = true"
									>《平台用户服务协议》</span
								>
								<!-- <span style="font-weight: bold;">购买提示：</span>
								<span>此商品一经售出，不予退换！</span> -->
							</div>
							<div class="buy-btn" @click="buyProduct">立即支付</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<my-dialog
			:dialogVisible="dialogVisible"
			title="卡号支付"
			@closeDialog="dialogVisible = false"
		>
			<div class="input-area">
				<span class="input-title">请填写卡号：</span>
				<el-input v-model="card_num" placeholder="请输入"></el-input>
			</div>
			<div class="btn-area">
				<button class="square-btn " @click="dialogVisible = false">取消</button>
				<button class="square-btn primary split-area" @click="buyProductByCard">
					确定
				</button>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="payInfoVisible"
			title="支付信息"
			@closeDialog="payInfoVisible = false"
			:width="800"
		>
			<div class="payInfo-wrapper">
				<img src="../../assets/images/pay_success.png" />
				<div class="flex-area-wrapper">
					<div class="flex-area">
						<div class="title">您购买的产品为：</div>
						<div class="value">{{ buyFinishInfo.productName }}</div>
					</div>
					<div class="flex-area">
						<div class="title">绑定的产品SN是：</div>
						<div class="value">{{ buyFinishInfo.snCode }}</div>
					</div>
					<div class="flex-area">
						<div class="title">您的激活码是：</div>
						<div class="value">
							<p id="copy">{{ buyFinishInfo.activeCode }}</p>
						</div>
					</div>
				</div>
			</div>
			<div class="btn-area">
				<button
					class="square-btn primary"
					@click="$router.push('/productList')"
				>
					确定
				</button>
				<button
					style="margin-left: 50px;"
					class="square-btn copy"
					data-clipboard-action="copy"
					data-clipboard-target="#copy"
					@click="copy"
				>
					复制激活码
				</button>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="wxDialog"
			title="支付方式"
			@closeDialog="beforeAliDialog"
			:width="800"
		>
			<div class="wxPay-head">
				<img src="../../assets/images/aliPayIcon2.png" class="wxPay-icon" />
				<span class="icon-text">支付宝支付</span>
			</div>
			<div class="wxPay-wrapper">
				<div ref="wxPay"></div>
				<div class="wxPay-text">
					<img src="../../assets/images/scan.png" class="scan-icon" />
					<div>
						<p>请使用支付宝"扫一扫"</p>
						<p>扫描二维码支付</p>
					</div>
				</div>
				<div class="wxPay-price">￥{{ totalPrice }}</div>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="TemasekDialog"
			title="平台用户服务协议"
			@closeDialog="TemasekDialog = false"
			:width="1000"
		>
			<div class="temasekWrapper">
				<p>
					浙江泰源产品网站上的商品由浙江泰源科技有限公司销售。如果您在产品网站购买产品，您便接受了以下交易条款。请仔细阅读这些条款。当您通过在泰源产品网站购买产品或使用其提供的服务，您同意接受本交易条款和所有有关的政策、条件和准则的约束。如果您不同意本交易条款中的任何一条，您可以选择不在泰源产品网站购买商品或接受服务。如您有任何疑问，请及时联系我们。
				</p>
				<h2>1.产品说明</h2>
				<p>
					泰源产品网站努力使产品说明尽可能准确。不过，我们并不保证产品说明或泰源产品网站的其他内容是准确的、完整的、可靠的、最新的或无错误的。如果泰源产品网站提供的产品本身并非如说明所说，您可通过网站提供的联系方式咨询。
				</p>
				<h2>2.合同的签订</h2>
				<p>
					泰源产品网站上的商品图片展示、说明和价格构成要约邀请。如果您通过我们网站订购产品，您的订单就成为一种购买产品的申请或要约。您下单购买支付货款后，我们双方的合同立即成立。对于泰源产品网站上的数字商品而言，由于其载体的特殊性，在您订购数字商品并支付相应价款后，关于数字商品的合同即成立。
				</p>
				<h2>3.退款政策</h2>
				<p>此商品一经售出，不予退换。</p>
				<h2>4.版权说明</h2>
				<p>
					泰源产品网站上的所有内容诸如文字、图表、标识、按钮图标、图像、声音文件片段、数字下载、数据编辑和软件、商标都是泰源产品网站的财产，受中国和国际版权法的保护。未经泰源产品网站书面授权或许可，不得以任何目的对泰源产品网站或其任何部分进行复制、复印、仿造、出售、转售、访问、或以其他方式加以利用。
				</p>
				<h2>5.隐私声明</h2>
				<p>
					泰源产品网站不会向任何第三方披露、转让、出租或者出售交易用户名单、交易记录等涉及消费者个人信息的数据以及您保存在泰源产品网站各项服务中的非公开内容，除非您明确授权泰源产品网站，或泰源产品网站在诚信基础上认为透露这些信息是必要的。您了解并同意，泰源产品网站有权根据相应法律法规的要求，或应政府部门（包括司法及行政部门）的要求，向其提供您在泰源产品网站填写的注册信息和交易纪录等必要信息。如您涉嫌侵犯他人知识产权，则泰源产品网站亦有权在初步判断涉嫌侵权行为存在的情况下，向权利人提供您必要的身份信息。
				</p>
				<h2>6.责任限制和不可抗力</h2>
				<p>
					不论在任何情况下，泰源产品网站均不对由于互联网正常的设备维护，互联网络连接故障，电脑、通讯或其他系统的故障，电力故障，罢工，暴乱，骚乱，火灾、洪水、风暴，爆炸，战争，政府行为，司法行政机关的命令或第三方的不作为而造成的不能履行或延迟履行承担责任。
				</p>
			</div>
		</my-dialog>
	</div>
</template>

<script>
import myDialog from "../../components/my_dialog";
import { getProductDetail } from "@/api/product";
import { baseUrl } from "../../../config";
import {
	cardPay,
	getInfoByOrderNo,
	getOrderId,
	requestWxPay,
	getOrderStatus,
	aliPay,
	getAliOrderStatus,
} from "@/api/pay";
import Clipboard from "clipboard";
import QRCode from "qrcodejs2";
export default {
	data() {
		return {
			product_logo: "",
			tableData: [
				{ product_num: 1, chName: "", enName: "", introduction: "", originalPrice: "", actualPrice: "" },
			],
			pay_arr: [
				{
					name: "支付宝支付",
					type: "icon",
					url: require("../../assets/images/aliPayIcon.png"),
					description: "支付宝付款",
					service_charge: "￥00.00",
				},
				// {
				// 	name: "微信支付",
				// 	type: "icon",
				// 	url: require("../../assets/images/wxPayIcon.png"),
				// 	description: "微信扫码付款",
				// 	service_charge: "￥00.00",
				// },
				{
					name: "卡号支付",
					type: "icon",
					url: require("../../assets/images/cardPayIcon.png"),
					description: "卡号付款",
					service_charge: "￥00.00",
				},
			],
			pay_way: "支付宝支付",
			active_nums: 1,
			sn_arr: [
				{
					value: "",
				},
			],
			is_invoice: false,
			options: [],
			invoice_type: "",
			is_agree: false,
			dialogVisible: false,
			card_num: "",
			payInfoVisible: false,
			baseUrl: "",
			buyFinishInfo: {
				productName: "",
				snCode: "",
				activeCode: "jafjklajdfjasjdfklj",
			},
			wxDialog: false,
			TemasekDialog: false,
			aliPayTimer: null
		};
	},
	created() {},
	mounted() {
		this.baseUrl = baseUrl;
		this.init();
	},
	components: {
		myDialog,
	},
	computed: {
		totalPrice() {
			return (this.tableData[0].product_num * this.tableData[0].actualPrice).toFixed(2);
		},
	},
	watch: {
		active_nums(newVal, oldVal) {
			newVal = newVal.replace(/[^\d]/g, "");
			if (parseInt(newVal) > 20) {
				newVal = 20;
				this.$message({
					type: "warning",
					message: "最大购买数量为20个！",
				});
			}
			if (+newVal > +oldVal) {
				for (let i = oldVal; i < newVal; i++) {
					this.sn_arr.push({
						value: "",
					});
				}
			} else {
				this.sn_arr.splice(newVal);
			}
			this.active_nums = "" + newVal;
			this.tableData[0].product_num = newVal;
		},
	},
	methods: {
		// 将激活数量+1
		activeUp() {
			let tempNum = parseInt(this.active_nums) + 1;
			this.active_nums = String(tempNum);
		},
		// 将激活数量-1
		activeDown() {
			let tempNum = parseInt(this.active_nums) - 1;
			tempNum = tempNum < 1 ? 1 : tempNum;
			this.active_nums = String(tempNum);
		},
		// 复制激活码
		copy() {
			var clipboard = new Clipboard(".copy");
			clipboard.on("success", (e) => {
				this.$message({
					message: "复制成功",
					type: "success",
				});
				// 释放内存
				clipboard.destroy();
			});
			clipboard.on("error", (e) => {
				// 释放内存
				clipboard.destroy();
				this.$message({
					message: "该浏览器不支持自动复制",
					type: "fail",
				});
			});
		},
		// 在支付宝支付之前做相应事
		beforeAliDialog() {
			this.wxDialog = false;
		},
		// 购买产品
		buyProduct() {
			clearInterval(this.aliPayTimer);
			if (!this.is_agree) {
				this.$message({
					type: "warning",
					message: "请先阅读平台用户服务协议！",
				});
				return;
			}
			if (this.active_nums == "" || this.active_nums == "0") {
				this.$message({
					type: "warning",
					message: "请填写正确的激活数量！",
				});
				return;
			}

			for (let i = 0; i < this.sn_arr.length; i++) {
				if (this.sn_arr[i].value.replace(/\s+/g, "") == "") {
					this.$message({
						type: "warning",
						message: "产品SN必填！",
					});
					return;
				}
				if (!/^[A-Fa-f\d]{12}$/.test(this.sn_arr[i].value)) {
					this.$message({
						type: "warning",
						message: "产品SN格式错误！",
					});
					return;
				}
			}
			let sns = this.sn_arr.map((item) => {
				return item.value;
			});
			let tempSns = [...new Set(sns)];
			if (tempSns.length !== sns.length) {
				this.$message({
					type: "warning",
					message: "SN码不能相同！",
				});
				return;
			}
			if (this.pay_way == "支付宝支付") {
				let sns = this.sn_arr.map((item) => {
					return item.value;
				});
				let count = +this.active_nums
				let loading = this.$loading({
					lock: true,
					text: '提交订单中....',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
					});
				aliPay({
					count,
					productSeriesId: this.$route.params.id,
					sns,
					actualPrice: this.totalPrice,
				}).then((res) => {
					// console.log(res);
					loading.close();
					if(res.data.code == "401") {
						this.$message({
							type: "warning",
							message: "该产品已下架"
						})
						this.$router.push("/")
						return;
					}
					if(res.data.code == "400") {
						this.$message({
							type: "warning",
							message: res.data.data
						})
						return;
					}
					this.wxDialog = true;
					this.$nextTick(() => {
						this.$refs.wxPay.innerHTML = "";
						new QRCode(this.$refs.wxPay, {
							text: res.data.data.qrcode,
							width: 200,
							height: 200,
						});
					});
					this.aliPayTimer = setInterval(() => {
						getAliOrderStatus({
							out_trade_no: res.data.data.out_trade_no,
						}).then((result) => {
							if(result.data.code == "403") {
								this.$message({
									type: "warning",
									message: "订单超时！"
								});
								this.wxDialog = false;
								return;
							}
							if(result.data.code == "200") {
								clearInterval(this.aliPayTimer);
								this.handleBuyFinish(count, res.data.data.out_trade_no);
								return;
							}
						}).catch((e) => {
							console.log(e);
						})
					}, 2000);
				});

